<template>
    <div class="about">
        <img :src="banner" class="img"/>
        <div class="navigation">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7 pointer" @click="navigator('/')">{{ $t('home') }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7 pointer"  @click="navigator('/case')">{{ $t('case') }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">{{ products[index].product_name }}</div>
        </div>
        <div class="card">
            <div class="content">
                <div class="title">{{ products[index].product_name }}</div>
                <div class="text">{{ $indent(products[index].product_description) }}</div>
                <img :src="products[index].product_image_url" class="ls"/>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { useRoute, useRouter } from 'vue-router';
    import { useStore } from 'vuex';
    import { ref, computed, onMounted } from 'vue';
    const store = useStore();
    const route = useRoute();
    const router = useRouter();
    let index = ref(route.query.index ? Number(route.query.index) : 0);
    const products = computed(() => store.state.info?.case[0].items ?? []);
    const banner = computed(() => {
        if (store.state.info.navigation) {
            let obj = store.state.info.navigation.find(el => el.router === '/case')
            document.title = `${ store.state.info.products[0].items[route.query.index].product_name }-${ obj.title }-${ store.state.info.company_name }`
            return obj.banner
        }
        return ''
    });
    const navigator = (path) => {
        router.push(path)
    }
</script>
